:host {
  display: block;
}

:host>.form-check {
  padding-left: 0.25rem;
}

:host>.form-check.form-switch {
  padding-left: 2.75rem;
}

:host>.form-check.form-switch .form-check-input {
  margin-top: .3rem;
}

:host>.form-check.form-check-inline {
  margin-right: .75rem;
}

:host>.form-check:not(.form-switch) .form-check-input[type=checkbox] {
  margin-top: 0;
}

:host .input-group.input-group-static label {
  margin-left: .25rem;
  margin-top: .15rem;
}

:host label {
  cursor: pointer;
}